/* App.css */

html {
  margin: 0 auto;
  max-width: 1440px;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: white; /* White background */
  max-width: 1440px;
}

header {
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: sticky;
  top: 0;
  z-index: 1000;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
}

header img {
  margin-left: 36px;
}

.logo {
  height: 143px;
}

nav {
  margin-right: 40px;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
}

nav ul li {
  margin-right: 20px;
}

nav ul li a {
  text-decoration: none;
  color: #6a0dad;
  font-size: 25px;
  padding-left: 26px;
  position: relative;
  top: 21px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
}

nav a::after {
  content: "";
  position: absolute;
  left: 25px;
  bottom: -8px;
  width: 70%;
  height: 2px;
  background-color: #6a0dad;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

nav a:hover::after {
  transform: scaleX(
    1
  ); /* Étend la ligne à 100% de la largeur du lien au survol */
}

.separator {
  background-color: #5d18e8;
  height: 2px;
  margin: 0px auto;
  opacity: 0.7;
  width: 98%;
  position: relative;
}

.banner {
  padding-top: 200px;
  margin-top: -200px;
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  position: relative;
  bottom: 20px;
}
.banner img {
  width: 100%;
}

.about-section {
  display: flex;
  align-items: center;
  background: aliceblue;
  box-sizing: border-box;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  padding-top: 200px;
  margin-top: -200px;
}

.aboutcontainer {
  display: flex;
  justify-content: center;
  background: aliceblue;
  padding: 0px 120px;
  padding-bottom: 160px;
  padding-top: 140px;
  box-sizing: border-box;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
}

.about-section div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-section p {
  max-height: 400px;
}

.about-section img.audrey1 {
  margin-right: 78px;
  height: 47vh;
  border-radius: 75px;
  position: relative;
  top: 60px;
}

.contact-section {
  width: 100vw;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  background-image: url(./assets/contact.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
}

.contacttext {
  position: absolute;
  right: 350px;
  text-align: center;
}

.contacttext p {
  color: black;
}

.contactimg {
  width: 100%;
}

.contact-section h2 {
  font-family: "Montserrat";
  font-size: 35px;
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 15px;
  letter-spacing: 0.4px;
  color: black;
  text-decoration-line: underline;
  position: relative;
  bottom: 50px;
}

.rdv {
  border-radius: 75px;
  background-color: lightslategray;
  color: white !important;
  padding: 20px;
}

.contact-section p {
  font-size: 25px;
  line-height: 30px;
  margin: 60px 0px;
}

.contact-section i {
  margin-right: 24px;
}

.contact-section a {
  text-decoration: none;
  color: black;
  font-size: 36px;
  line-height: 51px;
  transition: transform 0.3s ease;
}

.rdv:hover {
  transform: scaleX(1.1) !important;
  font-size: 37px;
}

.fa-brands {
  font-size: 50px;
  transition: transform 0.3s ease;
}

.fa-brands:hover {
  transform: scaleX(1.1);
}

h2,
